<template>
    <div :id="getId()" :class="`arbitration-chain ${getColor(chain.risk)} ${fullClass}`" :style="fullStyle">
        <div class="highlight-block" v-if="showHighlight"></div>
        <img v-if="viewFull===false" @click="changeView()" class="image-button" src="@/assets/newImages/arbitration_plus.png">
        <img v-if="viewFull===true" @click="changeView()" class="image-button" src="@/assets/newImages/arbitration_minus.png">
        <div :class="viewFull===false?'coins-flex':'coins-flex-vertical'">
            <div :class="viewFull===false?'coin-flex':'coin-flex-vertical'" v-for="(ticker, idx) in chain.tickers" :key="idx">
                <div v-if="chain.currentTickerIndex===idx" :class="viewFull===false?'active-ticker':'active-ticker-vertical'"
                :style="`opacity: ${activeTickerOpacity};`" >
                </div>
                <div class="coin-wrapper">
                    <div class="coin-image-block">
                        <img :class="viewFull===false?'coin-image':'coin-image-vertical'" :src="require(`@/assets/newImages/coins/vectors/${ticker}.svg`)">
                        <div class="coin-highlight-block" v-if="chain.currentTickerIndex===idx"></div>
                    </div>
                    
                    <div v-if="viewFull===true" class="coin-name">{{getCoinName(ticker)}}</div>
                </div>
                <img :class="viewFull===false?'coin-arrow':'coin-arrow-vertical'" v-if="idx !== chain.tickers.length-1" :src="getArrow(idx)">
            </div>
            
        </div>

        <div class="arbitration-chain-grid">
            <div class="arbitration-chain-cell cell-1" v-if="!chain.depositedFirstCoin">
                <div class="buttons-block">
                    <div class="basic-button" @click="deposit">{{$localizeFilter('start')}}</div>
                </div>
            </div> 

            <div class="arbitration-chain-cell cell-2">
                <div class="text-block">
                    <div class="title">
                        {{$localizeFilter('risk')}}
                    </div>
                    <div class="text">
                        {{`${chain.risk}%`}}
                    </div>
                </div>
            </div>

            <div class="arbitration-chain-cell cell-3">
                <div class="text-block">
                    <div class="title">
                        {{$localizeFilter('interest')}}
                    </div>
                    <div class="text">
                        {{`${chain.interest} %`}}
                    </div>
                </div>
            </div>

            <div class="arbitration-chain-cell cell-4">
                <div class="text-block">
                    <div class="title">
                        {{chain.estimatedExecutionTime ? $localizeFilter('execution_time') : $localizeFilter('until_finish')}}
                    </div>
                    <div class="text">
                        {{`~${getTimeString(chain.estimatedExecutionTime || chain.estimatedTimeUntilFinish)}`}}
                    </div>
                </div>
            </div>

            <div class="arbitration-chain-cell cell-5" v-if="chain.depositFirstCoinLimit!=100000">
                <div class="text-block">
                    <div class="title">
                        {{chain.depositFirstCoinLimit ? $localizeFilter('limit') : $localizeFilter('deposited')}} 
                    </div>
                    <div class="text" v-html="limitText" :style="`white-space: pre-wrap;`">
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        chain: {
            type: Object,
            required: true
        }, 
        type: {
            type: String,
            required: true
        },
        isNewChain: {
            type: Boolean,
            default: false
        },
        coinsNames: {
            type: Object,
            default: () => {}
        }, 
    },
    watch: { 
        isNewChain(newVal, oldVal) {
            this.$nextTick(()=>{
                this.animateHighlight()
            })
        }
    },
    data: ()=> ({
        currentOrangeArrowIdx: 0,
        activeTickerOpacity: 1,
        activeTickerIsGettingBrighter: false,
        interval1: null,
        interval2: null,
        viewFull:false,
        showHighlight: false,
        maxHeight: 350,
        fullMaxHeight: 2000,
        mountDate: +Date.now(),
        fullClass: '',
        fullStyle: ''
    }),
    computed: {
        limitText() {
            let res = '';
            if(this.chain.depositFirstCoinLimitMin){
                res += this.chain.depositFirstCoinLimitMin;
            }
            if(this.chain.depositFirstCoinLimitMin && this.chain.depositFirstCoinLimit) {
                res += '&nbsp;-&nbsp;';
            }
            if(this.chain.depositFirstCoinLimit){
                res += this.chain.depositFirstCoinLimit;
            }
            res += `&nbsp;${this.chain.tickers[0]}`;
            return res;
        },
    },
    methods: {
        changeView() {
            this.fullClass = (this.viewFull ? '' : 'full')
            this.fullStyle = (this.viewFull ? `max-height: ${this.maxHeight}px;` :  `max-height: ${this.fullMaxHeight}px;`)
            console.log(this.fullClass, this.fullStyle)
            setTimeout(()=>{
                this.viewFull=!this.viewFull
            },0)
        },
        getId() {
           return `arbitration-chain-${this.type}-chain${this.chain.chainId}-${this.mountDate}`
        },
        getCoinName(ticker) {
            if (this.coinsNames !== null && this.coinsNames !== undefined) {
                return this.coinsNames[ticker] || ticker
            } else {
                return ticker
            }
        },
        animateHighlight() {
            if (this.showHighlight === false) {
                this.showHighlight = true
                setTimeout(()=>{
                    this.showHighlight = false
                }, 22000)
            }
        },
        getTimeString(miliseconds) {
            if (miliseconds / 3600000 >= 1) {
                const hours = Math.ceil(miliseconds / 3600000)
                return localizeFilter('hours_short', [hours])
            } else {
                const minutes = Math.ceil(miliseconds / 60000)
                return localizeFilter('minutes_short', [minutes])
            }
        },
        getArrow(idx) {
            if (this.type === 'chains_in_work') {
                if ((idx === this.currentOrangeArrowIdx-1) || (idx === this.currentOrangeArrowIdx+1)) {
                    return require('@/assets/newImages/arbitrationArrows/arrow_green.png')
                } else if (idx === this.currentOrangeArrowIdx) {
                    return require('@/assets/newImages/arbitrationArrows/arrow_orange.png')
                } else {
                    return require('@/assets/newImages/arbitrationArrows/arrow_blue.png')
                }
            } else {
                return require('@/assets/newImages/arbitrationArrows/arrow_blue.png')
            }
        },
        deposit() {
            this.$emit('deposit', this.chain)
        },
        getColor(risk) {
            if (risk < 2) { return 'risk-green' }
            else if (risk < 3.5) { return 'risk-yellow' }
            else if (risk < 5) { return 'risk-orange' }
            else { return 'risk-red' }
        },
    },
    mounted() {
        this.maxHeight = document.getElementById(this.getId()).offsetHeight

        this.fullClass = (this.viewFull ? 'full' : '')
        this.fullStyle = (this.viewFull ? `max-height: ${this.fullMaxHeight}px;` : `max-height: ${this.maxHeight}px;`)

        if (this.type === 'chains_in_work') {
           // console.log(this.chain)
            this.interval1 = setInterval(()=>{
                if (this.currentOrangeArrowIdx < this.chain.tickers.length-2) {
                    this.currentOrangeArrowIdx = this.currentOrangeArrowIdx + 1
                } else {
                    this.currentOrangeArrowIdx = 0
                }
            }, 2000)

            this.interval2 = setInterval(()=>{
                if (this.activeTickerOpacity >= 1) {
                    this.activeTickerIsGettingBrighter = false
                } else if (this.activeTickerOpacity <= 0) {
                    this.activeTickerIsGettingBrighter = true
                }
                    if (this.activeTickerIsGettingBrighter) {
                        this.activeTickerOpacity += 0.1
                    } else {
                        this.activeTickerOpacity -= 0.1
                    }
            }, 100)
        }
    },
    unmounted() {
        if (this.type === 'chains_in_work') {
            clearInterval(this.interval1)
            this.interval1 = null
            clearInterval(this.interval2)
            this.interval2 = null
        }
    }
})
</script>

