<template>
    <div class="amount">
        <div :class="myPoolsBalance ? 'title title-pools-balance' : 'title'">{{ title.replace(':', '') + ' \\' }}</div>
        <div :class="`amount-block ${color} ${classOverride} ${classByType}`">
            <div class="highlight-block" v-if="showHighlight"></div>
            <img class="amount-image" :src="amountImage">
            <div class="amount-num">{{ formattedAmount }} {{ unit }}</div>
        </div>
        <div class="withdraw-block absolute" v-if="classOverride === 'withdraw-available'">
            <div class="withdraw-left-block">
                <div class="available-to-withdraw">{{ $localizeFilter('available_to_withdraw',
                    [`${$utils.formatNumber(availableProfitToWithdraw, 'en-US', 6, 6)}&nbsp;USDT`]) }}</div>
                <div class="comission">{{ $localizeFilter('comission_1_usdt') }}</div>
            </div>
            <button class="underline-button withdraw-button" @click="liquidityProvidingWithdrawProfit">
                {{ $localizeFilter('withdraw') }}
            </button>

        </div>

        <div class="withdraw-block absolute" v-if="type === 'gift'">
            <div class="withdraw-left-block" v-if="giftPercent">
                <div class="available-to-withdraw">{{ $localizeFilter('give_a_present_as_percent',
                    [`${$utils.formatNumber(giftPercent, 'en-US', 1, 1)}`]) }}</div>
                <div class="comission">{{ $localizeFilter('give_a_present_as_percent_subtitle') }}</div>
            </div>
            <div class="withdraw-left-block" v-else>
                <div class="available-to-withdraw">{{ $localizeFilter('give_a_present_not_avalilable') }}</div>
                <div class="comission">{{ $localizeFilter('give_a_present_not_avalilable_subtitle') }}</div>
            </div>
            <button class="underline-button" @click="showGiftModal" v-if="(amount >= 0) && (giftPercent >= 0)"
                :class="amount === 0 ? 'inactive' : ''">
                {{ $localizeFilter('send') }}
            </button>

        </div>
        <audio>
            <source src="@/assets/audio/income_sound.mp3" type="audio/mpeg">
        </audio>

        <!-- <button @click="play">Play sound</button> -->
    </div>
</template>

<script>
import { booleanLiteral, stringLiteral } from '@babel/types';
import { defineComponent } from '@vue/composition-api'
import anime from 'animejs/lib/anime.es.js';
import utils from '@/utils/utils'
import localizeFilter from '../filters/localize.filter';

const sound = require("@/assets/audio/income_sound.mp3");

export default defineComponent({
    props: {
        myPoolsBalance: {
            type: Boolean,
        },
        classOverride: {
            type: String,
            default: ''
        },
        amount: {
            type: Number,
            required: true
        },
        availableProfitToWithdraw: {
            type: Number,
            default: 0
        },
        title: {
            type: String,
            required: true
        },
        color: {
            type: String,
            required: true
        },
        type: {
            type: String,
            default: null
        },
        soundEnabled: {
            type: Boolean,
            default: false
        },
        giftPercent: {
            type: Number,
            default: 0
        }
    },
    watch: {
        amount(newVal, oldVal) {
            // console.log('updated amount, oldVal =', oldVal, ', newVal =', newVal)
            if ((newVal !== oldVal)) {
                this.$nextTick(() => {
                    if (this?.type === 'income') {
                        if (this.index > 0) {
                            this.count = oldVal
                            this.setCount(newVal)
                        } else {
                            this.index = 1
                            this.count = newVal
                        }
                    } else if (this.type === 'arbitrationTotalIncome') {
                        if (this.index > 0 && newVal > oldVal) {
                            this.count = oldVal
                            this.setCount(newVal)
                            this.animateHighlight()
                            // play sound
                            if (this.soundEnabled) {
                                this.play()
                            }
                        } else {
                            this.index = 1
                            this.count = newVal
                        }
                    } else {
                        this.count = newVal
                    }
                })
            }
        }
    },
    setup() {
        const audio = new Audio(sound);
        audio.muted = true
        function play() {
            // console.log('play', audio)
            audio.muted = false
            audio.play()
        }
        return { play }
    },
    data: () => ({
        count: 0.0,
        index: 0,
        showHighlight: false
    }),
    methods: {
        showGiftModal() {
            this.$emit('showGiftModal')
        },
        animateHighlight() {
            if (this.showHighlight === false) {
                this.showHighlight = true
                setTimeout(() => {
                    this.showHighlight = false
                }, 4500)
            }
        },
        setCount(val) {
            const obj = { n: this.count }
            anime({
                targets: obj,
                n: val,
                round: 1,
                duration: 3000,
                easing: 'linear',
                update: () => {
                    this.count = obj.n
                }
            })
        },
        async liquidityProvidingWithdrawProfit() {
            try {
                await this.$store.dispatch('liquidityProvidingWithdrawProfit')
                this.$message(localizeFilter('liquidity_withdraw_successfull'))
                this.$emit('reloadData')
            }
            catch (e) {
                this.$error(e)
            }

        },
    },
    computed: {
        formattedAmount() {
            return this.type === 'gift' ? this.count : (this.count / 1000000).toFixed(6)
            // return this.count ? this.count.toString().slice(0, this.count.toString().length - 6) + '.' + this.count.toString().slice(this.count.toString().length - 6, this.count.toString().length) : this.count;
        },
        amountImage() {
            return this.type === 'gift' ? require('@/assets/newImages/gift_icon.png') : require('@/assets/newImages/coins/usdt-trc20.png')
        },
        classByType() {
            return this.type === 'gift' ? 'gift' : ''
        },
        unit() {
            return this.type === 'gift' ? localizeFilter('gifts') : 'USDT'
        }
    },
    mounted() {
        // console.log('mounted, amount =', this.amount)
        if (this.type !== 'income') {
            this.count = this.amount
        }
    },
})
</script>

